.login{
    padding-bottom: 100px;
    background-color: #f3f3f3;
      .login-content{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin-top: 100px;
        width: 50%;
        padding: 80px 0;
        border-radius: 60px;
        background-color: $secondaryColor;
        .login-title {
            padding: 15px;
            font-size: 22px;
            font-weight: 600;
            text-align: center;
          }
          .login-form {
            display: grid;
            grid-template-columns: 1fr;
            row-gap: 16px;
            label {
                display: block;
            margin-bottom: 8px;
            }
            input {
                width: 100%;
                padding: 1.2rem;
                border-radius: 9px;
                border: none;
                &:focus{
                    outline: none;
                    box-shadow: 0 0 0 4px rgba(253, 242, 233, 0.5);
                }
              }
            
            a{
                text-decoration: underline;
                color: #000;
            }
          }       
      .btn--form {
        background-color: #f48982;
        color: #fdf2e9;
        align-self: end;
        padding: 8px;
      }   
      .btn,
      .btn:link,
      .btn:visited {
        display: inline-block;
        text-decoration: none;
        font-size: 20px;
        font-weight: 600;
        border-radius: 9px;
        border: none;
        cursor: pointer;
        transition: all 0.3s;
      }     
      button {
        outline: 1px solid #f48982;
      } 
      .btn--form:hover {
        background-color: #fdf2e9;
        color: #f48982;
        }
      } 
}